@charset "UTF-8";
/**
* @fileoverview style for content
* @author NHN FE Development Lab <dl_javascript@nhn.com>
*/

.CodeMirror {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
.tui-editor-contents *:not(table) {
  line-height: 160%;
  box-sizing: content-box;
}

.tui-editor-contents i,
.tui-editor-contents cite,
.tui-editor-contents em,
.tui-editor-contents var,
.tui-editor-contents address,
.tui-editor-contents dfn {
  font-style: italic;
}

.tui-editor-contents strong {
  font-weight: bold;
}

.tui-editor-contents p {
  margin: 10px 0;
  color: var(--tui-color-p);
}

.tui-editor-contents > h1:first-of-type,
.tui-editor-contents > div > div:first-of-type h1 {
  margin-top: 14px;
  text-align: center;
}

.tui-editor-contents h1,
.tui-editor-contents h2,
.tui-editor-contents h3,
.tui-editor-contents h5 {
  font-weight: bold;
}

.tui-editor-contents h1 {
  font-size: 1.6rem;
  line-height: 28px;
  border-bottom: 3px double #999;
  margin: 52px 0 15px 0;
  padding-bottom: 7px;
  color: var(--tui-color-h1);
}

.tui-editor-contents h2 {
  font-size: 1.3rem;
  line-height: 23px;
  border-bottom: 1px solid #dbdbdb;
  margin: 30px 0 13px 0;
  padding-bottom: 7px;
  color: var(--tui-color-h);
}

.tui-editor-contents h3,
.tui-editor-contents h4 {
  font-size: 1.2rem;
  line-height: 18px;
  margin: 20px 0 10px;
  color: var(--tui-color-h);
}

.tui-editor-contents h5,
.tui-editor-contents h6 {
  font-size: 1rem;
  line-height: 17px;
  margin: 10px 0 -4px;
  color: var(--tui-color-h);
}

.tui-editor-contents blockquote {
  margin: 15px 0;
  border-left: 4px solid #dddddd;
  padding: 0 15px;
  color: var(--tui-color-bq);
}

.tui-editor-contents blockquote > :first-child {
  margin-top: 0;
}

.tui-editor-contents blockquote > :last-child {
  margin-bottom: 0;
}

.tui-editor-contents pre,
.tui-editor-contents code {
  font-family: Consolas, Courier, 'Apple SD 산돌고딕 Neo', -apple-system,
    'Lucida Grande', 'Apple SD Gothic Neo', '맑은 고딕', 'Malgun Gothic',
    'Segoe UI', '돋움', dotum, sans-serif;
  border: 0;
  border-radius: 0;
}

.tui-editor-contents pre {
  margin: 2px 0 8px;
  padding: 18px;
  background-color: var(--tui-bg-pre);
}

.tui-editor-contents code {
  color: var(--tui-color-code);
  padding: 4px 4px 2px 0;
  letter-spacing: -0.3px;
}

.tui-editor-contents pre code {
  padding: 0;
  color: var(--tui-color-code);
  white-space: pre-wrap;
  background-color: transparent;
}

.tui-editor-contents pre.addon {
  border: 1px solid var(--tui-border-pre);
  background-color: var(--tui-bg-main);
}

.tui-editor-contents img {
  margin: 4px 0 10px;
  box-sizing: border-box;
  vertical-align: top;
  max-width: 100%;
}

.tui-editor-contents table {
  margin: 2px 0 14px;
  color: var(--tui-color-table);
  width: auto;
  border-collapse: collapse;
  box-sizing: border-box;
}

.tui-editor-contents table th,
.tui-editor-contents table td {
  height: 32px;
  padding: 5px 14px 5px 12px;
}

.tui-editor-contents table td {
  border: 1px solid var(--tui-color-td);
}

.tui-editor-contents table th {
  border: 1px solid var(--tui-border-th);
  border-top: 0;
  background-color: var(--tui-bg-th);
  font-weight: 300;
  color: var(--tui-color-main);
  padding-top: 6px;
}

.tui-editor-contents ul,
.tui-editor-contents menu,
.tui-editor-contents ol,
.tui-editor-contents dir {
  display: block;
  list-style-type: disc;
  padding-left: 17px;
  margin: 6px 0 10px;
  color: var(--tui-color-table);
}

.tui-editor-contents ol {
  list-style-type: decimal;
}

.tui-editor-contents ul ul,
.tui-editor-contents ul ol,
.tui-editor-contents ol ol,
.tui-editor-contents ol ul {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
}

.tui-editor-contents ul li,
.tui-editor-contents ol li {
  position: relative;
}

.tui-editor-contents ul p,
ol p {
  margin: 0;
}

.tui-editor-contents ul li.task-list-item:before,
.tui-editor-contents ol li.task-list-item:before,
.tui-editor-contents pre ul li:before {
  content: '';
}

.tui-editor-contents hr {
  border-top: 1px solid var(--tui-border-hr);
  margin: 16px 0;
}

.tui-editor-contents a {
  text-decoration: underline;
  color: #5286bc;
}

.tui-editor-contents a:hover {
  color: #007cff;
}

.tui-editor-contents {
  font-size: 13px;
  margin: 0;
  padding: 0;
}

.tui-editor-contents .task-list-item {
  border: 0;
  list-style: none;
  padding-left: 22px;
  margin-left: -22px;
}

.tui-editor-contents .task-list-item:before {
  background-repeat: no-repeat;
  background-size: 16px 16px;
  background-position: center;
  content: '';
  height: 18px;
  width: 18px;
  position: absolute;
  left: 0;
  top: 1px;
  cursor: pointer;
  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA4AAAAOCAYAAAAfSC3RAAAAAXNSR0IArs4c6QAAADdJREFUKBVjvHv37n8GMgALSI+SkhJJWu/du8fARJIOJMWjGpECA505GjjoIYLEB6dVUNojFQAA/1MJUFWet/4AAAAASUVORK5CYII=');
}

.tui-editor-contents .task-list-item.checked:before {
  background-image: url('data:image/png;base64,');
}

.tui-editor-contents .task-list-item input[type='checkbox'],
.tui-editor-contents .task-list-item .task-list-item-checkbox {
  margin-left: -17px;
  margin-right: 3.8px;
  margin-top: 3px;
}

.tui-editor-contents-placeholder:before {
  content: attr(data-placeholder);
  color: grey;
  line-height: 160%;
  position: absolute;
}
